/* to get pixelated images (nearest-neighbor filter) on all  browsers */
.rpgui-pixelated {
    -ms-interpolation-mode: nearest-neighbor;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: -webkit-crisp-edges;
    image-rendering: -moz-crisp-edges;
    image-rendering: -o-crisp-edges;
    image-rendering: pixelated;
}

/* unselectable text */
.rpgui-noselect {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* center things */
.rpgui-center {
    text-align: center;
    align-content: center;
}

/* rotate object 90 degrees */
.rpgui-rotate-90 {
    /* rotate 90 degrees */
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    /* rotate from center-left side */
    -ms-transform-origin: 0% 50%;
    /* IE 9 */
    -webkit-transform-origin: 0% 50%;
    /* Chrome, Safari, Opera */
    transform-origin: 0% 50%;
}

/**
* Styling for buttons
*/
/* button style */
.rpgui-button {
    /* hide button default stuff */
    background-color: Transparent;
    background-repeat: no-repeat;
    border: none;
    overflow: hidden;
    outline: none;
    /* background */
    background: url("../assets/button.png") no-repeat no-repeat;
    background-clip: padding-box;
    background-origin: padding-box;
    background-position: center;
    background-size: 100% 100%;
    /* font size */
    font-size: 1.0em;
    /* default size and display */
    max-width: 100%;
    min-width: 140px;
    height: 60px;
    display: inline-block;
    /* padding */
    padding-left: 35px;
    padding-right: 35px;
}

/* button hover */
.rpgui-button.hover,
.rpgui-button:hover {
    background-image: url("../assets/button-hover.png");
}

/* button clicked */
.rpgui-button.down,
.rpgui-button:active {
    background-image: url("../assets/button-down.png");
}

/* golden button stuff */
.rpgui-button.golden p {
    display: inline-block;
}

/* golden button style */
