}

.rpgui-button.golden:before {
    white-space: nowrap;
    display: inline-block;
    content: "";
    width: 34px;
    display: block;
    height: 110%;
    background: transparent url("../assets/button-golden-left.png") no-repeat right center;
    background-size: 100% 100%;
    margin: 0 0 0 0;
    left: 0px;
    float: left;
    margin-left: -46px;
    margin-top: -5%;
}

.rpgui-button.golden:after {
    white-space: nowrap;
    display: block;
    content: "";
    width: 34px;
    height: 110%;
    background: transparent url("../assets/button-golden-right.png") no-repeat left center;
    background-size: 100% 100%;
    margin: 0 0 0 0;
    right: 0px;
    float: right;
    margin-right: -46px;
    margin-top: -5%;
}

