    height: 24px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.rpgui-content input[type=checkbox].rpgui-checkbox:checked + label {
    background: url("../assets/checkbox-on.png") no-repeat;
    line-height: 24px;
    display: inline-block;
    background-size: auto 100%;
