* {
    font-family: 'MedievalSharp', cursive;
    font-size: 24px;
}

html, body {
    color: #FAEBD7;
    background-color: #222;
}

strong {
    color: #FAEFE1;
}

@media screen and (max-width: 768px) {
    .frame {
        min-width: 380px;
    }
}

.nowrap {
    white-space: nowrap;
}

.column {
    display: inline-block;
    vertical-align: top;
    padding-right: 20px;
}

.row {
    text-align: center;
}

.craeft-logo {
    font-size: 90px;
    font-weight: bold;
}

.craeft-logo a {
    color: #FAEFE1;
}

.craeft-logo a:hover {
    text-decoration: none;
}

.player {
    margin-bottom: 30px;
}

.player hr {
    margin-bottom: 18px;
}

.item,
.resources,
.craefter,
.craeft-window,
.craefter-window {
    border: 1px solid #FAEFE1;
    margin-bottom: 5px;
    margin-top: 2px;
    padding: 6px;
}

.craefters-list {
    margin-top: 5px;
}

.items hr,
.craefters hr,
.farm hr {
    margin-top: 10px;
    margin-bottom: 0;
}

.footer {
    text-align: center;
    background-color: inherit;
    padding: 10px 0 50px;
}

.about .framed-grey {
    margin-bottom: 20px;
}

.about blockquote:before {
    content: open-quote;
}

.about blockquote:after {
    content: close-quote;
}

.about .inspiration-item {
    padding-bottom: 20px;
}

.about .inspiration-item img {
    float: left;
    margin-right: 10px;
    margin-top: 5px;
}

.farm .columns,
.farm .column {
    padding: 0;
    margin: 0;
}

.item-description {
    min-width: 80%;
}

.equipment .columns {
    padding: 0;
}

.equipment .column {
    padding: 0;
    min-width: 140px;
}

.logs {
    min-height: 190px;
    text-align: left;
}

.rpgui-button {
    white-space: nowrap;
}

.slider-edge-value {
    position: relative;
    top: -35px;
}

.rtl {
    direction: rtl;
}

/*
Craefter
*/
.craefter-icon {
    position: relative;
    display: inline-block;
    border: 1px solid transparent;
    padding: 3px;
    line-height: 0px;
}

.craefter-icon-selected {
    border: 1px solid #FAEBD7;
}

.craefter-level {
    position: absolute;
    left: 8px;
    top: 8px;
    font-size: 18px !important;
}

.craefter-timeout {
    position: absolute;
    left: -9px;
    width: 80px;
    top: 28px;
    text-align: center;
}

/*
ITEM
*/
.item-container {
    line-height: 0;
    display: inline-block;
    padding: 2px !important;
}

.item-level {
    position: absolute;
    left: 5px;
    top: 5px;
    font-size: 18px !important;
}

.item-timeout {
    position: absolute;
    left: -9px;
    width: 80px;
    top: 28px;
    text-align: center;
}

.item-icon {
    border: 1px solid transparent;
    padding-top: 3px;
    padding-bottom: 3px;
}

.icon-small {
    width: 39px;
    height: 39px;
}

.item-icon-selected {
    border: 1px solid #FAEBD7;
}

.wood {
    filter: url("../assets/filters.svg#wood");
}

.metal {
    filter: url("../assets/filters.svg#metal");
}

.cloth {
    filter: url("../assets/filters.svg#cloth");
}

.diamond {
    filter: url("../assets/filters.svg#diamond");
}